import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useFirestore } from '../hooks/useFirestore';
import { addDoc, collection, serverTimestamp } from 'firebase/firestore';
import { db } from '../lib/firebase';
import { Search, Tag, MessageSquare, ExternalLink } from 'lucide-react';
import type { UserProfile } from '../types';
import { ProfilePicture } from '../components/ProfilePicture';

export function UserDirectory() {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSkills, setSelectedSkills] = useState<string[]>([]);
  const { data: users, loading, error } = useFirestore<UserProfile>('users');

  const filteredUsers = users.filter(profile => {
    if (profile.id === user?.uid) return false; // Don't show current user

    const matchesSearch = 
      profile.displayName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      profile.bio?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      profile.skills.some(skill => skill.toLowerCase().includes(searchTerm.toLowerCase()));
    
    const matchesSkills = selectedSkills.length === 0 || 
      profile.skills.some(skill => selectedSkills.includes(skill));
    
    return matchesSearch && matchesSkills;
  });

  const allSkills = Array.from(new Set(
    users.flatMap(profile => profile.skills)
  )).sort();

  const handleSkillToggle = (skill: string) => {
    setSelectedSkills(prev => 
      prev.includes(skill)
        ? prev.filter(s => s !== skill)
        : [...prev, skill]
    );
  };

  const handleStartChat = async (otherUser: UserProfile) => {
    if (!user) return;
    
    try {
      // Create or get conversation
      const conversationRef = await addDoc(collection(db, 'conversations'), {
        participants: [user.uid, otherUser.id],
        lastMessage: '',
        unreadCount: {
          [otherUser.id]: 0,
          [user.uid]: 0
        },
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp()
      });
      
      navigate(`/messages/${conversationRef.id}`);
    } catch (err) {
      console.error('Failed to start conversation:', err);
    }
  };

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-display font-bold text-white mb-2">
          User Directory
        </h1>
        <p className="text-gray-200">
          Find skilled professionals to collaborate with
        </p>
      </div>

      {/* Search and Filter */}
      <div className="mb-8 space-y-4">
        <div className="flex gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
            <input
              type="text"
              placeholder="Search by name, bio, or skills..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-3 bg-earth-800 border border-earth-700 rounded-lg 
                         text-gray-900 placeholder-gray-500
                         focus:border-accent-clay focus:ring-1 focus:ring-accent-clay/30 
                         transition-all duration-300"
            />
          </div>
        </div>

        {/* Skills Filter */}
        <div className="bg-earth-800 border border-earth-700 rounded-lg p-4">
          <h3 className="text-sm font-medium text-gray-900 mb-3 flex items-center gap-2">
            <Tag className="h-4 w-4" />
            Filter by Skills
          </h3>
          <div className="flex flex-wrap gap-2">
            {allSkills.map((skill) => (
              <button
                key={skill}
                onClick={() => handleSkillToggle(skill)}
                className={`px-3 py-1 rounded-full text-sm transition-all duration-300 ${
                  selectedSkills.includes(skill)
                    ? 'bg-accent-clay text-white'
                    : 'bg-earth-700 text-gray-900 hover:bg-earth-600'
                }`}
              >
                {skill}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Users Grid */}
      {loading ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse card p-6">
              <div className="flex items-center space-x-4 mb-4">
                <div className="h-12 w-12 bg-earth-700 rounded-full"></div>
                <div className="flex-1">
                  <div className="h-4 bg-earth-700 rounded w-3/4"></div>
                  <div className="h-3 bg-earth-700 rounded w-1/2 mt-2"></div>
                </div>
              </div>
              <div className="space-y-2">
                <div className="h-4 bg-earth-700 rounded"></div>
                <div className="h-4 bg-earth-700 rounded w-5/6"></div>
              </div>
            </div>
          ))}
        </div>
      ) : error ? (
        <div className="text-center py-8 text-red-500">Error loading users</div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredUsers.map((profile) => (
            <div
              key={profile.id}
              className="card p-6 card-hover"
            >
              {/* User Header */}
              <div className="flex items-center mb-4">
                <ProfilePicture url={profile.profilePicture} size="md" />
                <div className="ml-4">
                  <h3 className="text-lg font-medium text-gray-900">
                    {profile.displayName}
                  </h3>
                  {profile.portfolio && (
                    <a
                      href={profile.portfolio}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="text-sm text-accent-clay hover:text-accent-ochre flex items-center gap-1"
                    >
                      Portfolio <ExternalLink className="h-3 w-3" />
                    </a>
                  )}
                </div>
              </div>

              {/* Bio */}
              <p className="text-gray-700 mb-4 line-clamp-2">
                {profile.bio || 'No bio available'}
              </p>

              {/* Skills */}
              <div className="mb-4">
                <h4 className="text-sm font-semibold text-gray-900 mb-2">Skills</h4>
                <div className="flex flex-wrap gap-2">
                  {profile.skills.map((skill) => (
                    <span
                      key={skill}
                      className="px-2 py-1 bg-accent-sage/20 text-gray-900 text-sm rounded-full border border-accent-sage/30"
                    >
                      {skill}
                    </span>
                  ))}
                </div>
              </div>

              {/* Actions */}
              {user && (
                <button
                  onClick={() => handleStartChat(profile)}
                  className="w-full btn-primary flex items-center justify-center gap-2"
                >
                  <MessageSquare className="h-4 w-4" />
                  Message
                </button>
              )}
            </div>
          ))}

          {filteredUsers.length === 0 && (
            <div className="col-span-full text-center py-12">
              <div className="bg-earth-800 border border-earth-700 rounded-lg p-8">
                <Search className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <h3 className="text-xl font-display font-semibold text-gray-900 mb-2">
                  No users found
                </h3>
                <p className="text-gray-700">
                  Try adjusting your search or filters
                </p>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}