import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

const MySwal = withReactContent(Swal);

// Create a wrapper that ensures DOM is ready
const createAlert = () => {
  // Return a function that creates the alert
  return (options: any) => {
    // Ensure we're in a browser environment
    if (typeof window === 'undefined') return Promise.resolve();

    // Ensure DOM is ready
    if (document.readyState === 'loading') {
      return new Promise((resolve) => {
        document.addEventListener('DOMContentLoaded', () => {
          resolve(MySwal.fire(options));
        });
      });
    }

    // DOM is ready, show alert
    return MySwal.fire(options);
  };
};

// Export the alert function
export const showAlert = createAlert();

// Common alert patterns
export const showSuccess = (title: string, text?: string) => {
  return showAlert({
    title,
    text,
    icon: 'success',
    confirmButtonColor: '#4F46E5',
    customClass: {
      container: 'font-body',
      popup: 'rounded-lg',
      title: 'text-xl font-display font-bold',
      confirmButton: 'px-6 py-2 rounded-lg text-white'
    }
  });
};

export const showError = (title: string, text?: string) => {
  return showAlert({
    title,
    text,
    icon: 'error',
    confirmButtonColor: '#DC2626',
    customClass: {
      container: 'font-body',
      popup: 'rounded-lg',
      title: 'text-xl font-display font-bold text-red-600',
      confirmButton: 'px-6 py-2 rounded-lg text-white'
    }
  });
};

export const showConfirm = (title: string, text: string) => {
  return showAlert({
    title,
    text,
    icon: 'warning',
    showCancelButton: true,
    confirmButtonColor: '#4F46E5',
    cancelButtonColor: '#6B7280',
    confirmButtonText: 'Yes',
    customClass: {
      container: 'font-body',
      popup: 'rounded-lg',
      title: 'text-xl font-display font-bold',
      confirmButton: 'px-6 py-2 rounded-lg text-white',
      cancelButton: 'px-6 py-2 rounded-lg'
    }
  });
};